.container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}

.container > div:last-of-type {
	width: calc(100% - 75px);
	margin-left: 15px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.container > div:last-of-type > span:first-of-type {
	font-weight: bolder;
	font-size: 18px;
}

.container > div:last-of-type > span:last-of-type {
	max-height: 40px;
	line-height: 20px;
	overflow: hidden;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
}

.container > div:last-of-type > span {
	width: 100%;
}



